body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
  text-align: center;
}
.countdown-timer-container{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-top: 4em;    
}

.timer-block{
    border: 1px solid grey;
    border-radius: 5px;
    width: 6em;
    height: 6em;
    margin: 0 1em
}

.timer-value{
    font-size: 3em;
    text-align: center;
}

.timer-label{
    text-align: center;
    color: grey;
}

.controls-container{
    margin: 1vh auto;
    text-align:center;
}

.start-button{
    background-color: green;
    color: white;
    border-radius: 5px;
    width: 6em;
    height: 2em;
}

.stop-button{
    background-color: red;
    color: white;
    border-radius: 5px;
    width: 6em;
    height: 2em;
}
.countdown-timer-input-container{
    margin-left: 3vw;
    width: -webkit-fit-content;
    width: -mox-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

label{
    display: inline-block;
    width: 80px;
}

.set-button{
    margin: 1vh auto;
    display: block;
}
